<template>
  <div class="team">
    <h1>{{ msg }}</h1>
    <ul>
      <li><router-link to="/view-game">View Game</router-link></li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'team',
  data () {
    return {
      msg: 'Yo yo!'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



